/* @import url('https://fonts.googleapis.com/css2?family=Inter:wght@100&display=swap'); */
/* @import 'node_modules/react-modal-video/scss/modal-video.scss'; */
.App {
    /* font-family: 'Krona One', serif; */
    font-family: 'Jura', sans-serif;
    text-align: center;
  }
  
  body {
    margin: 0;
    padding: 0;
    background: white;
    overflow: hidden;
    --secondary: rgb(161, 161, 161);
    --divider: #343434;
    font-family: 'Jura', sans-serif;
  }
  
  * {
    box-sizing: border-box;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  }
  
  #root {
    /* display: flex; */
    -webkit-justify-content: center;
            justify-content: center;
    pointer-events: auto;
    cursor: inherit ;
  }
  
  .screen {
    width: 100%;
    height: 100%;
  }
  
  .container {
    max-width: 990px;
    -webkit-flex: 1 1 100%;
            flex: 1 1 100%;
    padding: 45px 25px;
  }
  
  h1 {
    font-weight: bold;
    color: white;
    margin: 6px 0 12px;
  }
  
  .date {
    color: var(--secondary);
    font-size: 14px;
    text-transform: uppercase;
  }
  
  header {
    border-bottom: 1px solid var(--divider);
    position: relative;
  }
  
  .avatar {
    background: var(--divider);
    border-radius: 50%;
    position: absolute;
    bottom: 12px;
    right: 0;
    overflow: hidden;
  }
  
  .avatar,
  .avatar img {
    width: 40px;
    height: 40px;
  }
  
  .hlink{
    cursor: inherit;
  }

  ul,
  li {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .card-list {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-align-content: flex-start;
            align-content: flex-start;
  }
  
  .card {
    position: relative;
    padding: 25px;
    height: 460px;
    -webkit-flex: 0 0 40%;
            flex: 0 0 40%;
    max-width: 40%;
  }
  
  .card:nth-child(4n + 1),
  .card:nth-child(4n + 4) {
    -webkit-flex: 0 0 60%;
            flex: 0 0 60%;
    max-width: 60%;
  }
  
  .card:nth-child(odd) {
    padding-left: 0;
  }
  
  .card:nth-child(even) {
    padding-right: 0;
  }
  
  .card-content-container {
    width: 100%;
    height: 100%;
    position: relative;
    display: block;
    pointer-events: none;
    cursor: inherit ;
  }
  
  .card-content-container.open {
    cursor: inherit ;
    top: 0;
    left: 0;
    right: 0;
    position: fixed;
    z-index: 20;
    overflow: hidden;
    padding: 80px 0;
  }
  
  .card-content {
    pointer-events: auto;
    cursor: inherit ;
    position: relative;
    border-radius: 20px;
    background: rgba( 255, 255, 255, 0.15 );
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 9.0px );
    -webkit-backdrop-filter: blur( 9.0px );
    /* border: 1px solid rgba( 255, 255, 255, 0.18 ); */
    overflow: hidden;
    width: 100%;
    height: 100%;
    margin: 0 auto;
  }

  @-moz-document url-prefix(){
    .card-content{
      pointer-events: auto;
      cursor: inherit ;
      position: relative;
      border-radius: 20px;
      background: rgba( 255, 255, 255, 0.85 );
      box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
      /* border: 1px solid rgba( 255, 255, 255, 0.18 ); */
      overflow: hidden;
      width: 100%;
      height: 100%;
      margin: 0 auto;
    }
  }
  
  .open .card-content {
    height: auto;
    max-height: 90vh;
    max-width: 700px;
    width: auto;
    overflow-y: scroll;
    pointer-events: all;
    cursor: inherit ;
    scrollbar-width: none;
  }

  .open .card-content::-webkit-scrollbar {
    width: 1px;
    cursor: inherit ;
  }
  
  .card-open-link {
    position: absolute;
    cursor: inherit ;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }
  
  .card-image-container {
    position: absolute;
    top: 0;
    left: 0;
    overflow: hidden;
    height: 420px;
    max-width: 100vw;
  }
  
  .open .card-image-container,
  .open .title-container {
    z-index: 1;
  }
  
  .title-container {
    position: absolute;
    top: 15px;
    left: 15px;
    max-width: 400px;
  }

  .title {
    font-family: 'Jura', sans-serif;
    color: #343434;
    margin: 8px 0;
    font-size: 17px;
    font-weight: bold;
    display: block;
  }
  
  .open .title-container {
    top: 30px;
    left: 30px;
  }
  
  .category {
    color: #ec644b;
    font-size: 12px;
    font-family: 'Jura', sans-serif;
    text-transform: uppercase;
  }
  
  .overlay {
    z-index: 1;
    position: fixed;
    background: rgba(255, 255, 255, 0.8);
    will-change: opacity;
    top: 0;
    bottom: 0;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    width: 100vw;
  }
  
  .overlay a {
    display: block;
    position: fixed;
    cursor: inherit ;
    top: 0;
    bottom: 0;
    width: 100vw;
    left: 50%;
  
    -webkit-transform: translateX(-50%);
  
            transform: translateX(-50%);
  }
  
  .content-container {
    padding: 460px 35px 35px 35px;
    max-width: 700px;
    width: 90vw;
  }
  
  p {
    color: #343434;
    font-size: 17px;
    line-height: 28px;
  }
  
  @media only screen and (max-width: 800px) {
    .card {
      -webkit-flex: 0 0 50%;
              flex: 0 0 50%;
      max-width: 50%;
    }
  
    .card:nth-child(4n + 1),
    .card:nth-child(4n + 4) {
      -webkit-flex: 0 0 50%;
              flex: 0 0 50%;
      max-width: 50%;
    }
  }
  
  @media only screen and (max-width: 600px) {
    .card {
      -webkit-flex: 1 0 100%;
              flex: 1 0 100%;
      max-width: 100%;
      padding: 10px;
      padding-left: 0;
      padding-right: 0;
    }
  
    .card:nth-child(4n + 1),
    .card:nth-child(4n + 4) {
      -webkit-flex: 1 0 100%;
              flex: 1 0 100%;
      max-width: 100%;
    }
  
    .card-content-container.open {
      padding: 0;
    }
  }
  




